.menu-right-screen {
    position: fixed;
    right: 0;
    /* top: 160px; */
    top: 353px;

    width: 88px;
    min-height: 293px;

    background-color: #fff;

    z-index: 10000;
}

@media (max-width: 1024px) {
    .menu-right-screen {
        top: 132px;
    }
}

.list-menu-screen__item {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 98px;
}

.list-menu-screen__item-link-wrapper {
    width: 100%;
    min-height: 98px;
    height: 1px;
    padding: 0 7px;

    font-family: 'Proxima Nova';
    font-size: 10px;
    line-height: 1.5;
    font-weight: 300;
    color: #2e2e2e;
    text-transform: uppercase;
}

.list-menu-screen__item:nth-child(1) .list-menu-screen__item-link-wrapper {
    padding-top: 7px;
}

.list-menu-screen__item:nth-last-child(1) .list-menu-screen__item-link-wrapper {
    padding-bottom: 7px;
}

.list-menu-screen__item-wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;

    border: 1px solid #3499ea;
    border-bottom: none;
}

.list-menu-screen__item:nth-last-child(1) .list-menu-screen__item-wrapper {
    border-bottom: 1px solid #3499ea;
}
.list-menu-screen__item:hover {
    background-color: #3499ea;
}

.list-menu-screen__item:hover .list-menu-screen__item-image path {
    fill: #fff;
}

.list-menu-screen__item:hover .list-menu-screen__item-wrapper {
    border: 1px solid #ffffff;
}

.list-menu-screen__item-image {
    margin: 0 auto;
    margin-top: 12px;
}

.list-menu-screen__item-text {
    margin: 0 auto;
    margin-top: 8px;

    text-align: center;
}

.list-menu-screen__item:hover .list-menu-screen__item-text {
    color: #fff;
}

@media (max-width: 768px) {
    .menu-right-screen {
        position: relative;

        width: 100%;
        margin-bottom: 133px;
        min-height: auto;
    }

    /* .list-menu-screen__item-wrapper {
        flex-direction: row;
    } */

    .list-menu-screen {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .list-menu-screen__item {
        width: auto;
        flex-grow: 1;
    }

    .list-menu-screen__item-link-wrapper {
        padding: 7px;

        font-size: 16px;
    }

    .list-menu-screen__item-wrapper {
        border: 1px solid #3499ea;
    }

    /* .list-menu-screen__item-image {
        margin: auto 0;
        margin-left: 37%;
    }

    .list-menu-screen__item-text {
        margin: auto 0;
        margin-left: 10px;
    } */
}
