body {
    background-color: #f3f7fb;
}

/* временное решение */

.choosing-specialist {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 94px;
    padding-top: 20px;
}

.choosing-specialist__title {
    margin: 0 auto;

    font-family: "PlayfairDisplay", "Arial", sans-serif;
    font-size: 26px;
    font-weight: 400;
    line-height: 1.25;
    color: #2e2e2e;
}

.choosing-specialist__container {
    min-height: 1419px;
    width: 100%;
    max-width: 1270px;
    margin: 0 auto;
    padding: 0 20px;
}

.choosing-specialist__list-tabs .slick-list {
    overflow: hidden;
    padding-top: 25px;
    min-height: 194px;
}

.choosing-specialist__list-tabs .slick-track{
    display: flex;
    justify-content: space-between;
}

.choosing-specialist__list-tabs .slick-arrow {
    position: absolute;
    top: 59px;

    width: 40px;
    height: 40px;

    font-size: 0;

    background-color: #ffffff;
    background-repeat: no-repeat;
    background-position: 13px 13px;
    border: none;
    border-radius: 30px;

    z-index: 1;
}

.choosing-specialist__list-tabs .slick-slider .slick-arrow:hover {
    background-color: #3499ea;
}

.choosing-specialist__list-tabs .slick-slider .slick-prev {
    left: 2px;

    background-image: url('../../image/health/button-prev.svg');
}

.choosing-specialist__list-tabs .slick-slider .slick-prev:hover {
    background-image: url('../../image/health/button-prev-active.svg');
}

.choosing-specialist__list-tabs .slick-slider .slick-next {
    right: 2px;

    background-image: url('../../image/health/button-next.svg');
}

.choosing-specialist__list-tabs .slick-slider .slick-next:hover {
    background-image: url('../../image/health/button-next-active.svg');
}

.choosing-specialist__list-tabs .slick-slider {
    position: relative;

    min-width: 190px;
    min-height: 194px;
    margin-top: 32px;
    padding: 0 6px;
}


.choosing-specialist__list-tabs .slick-slide {
    position: relative;
    max-width: 150px;
}

.tabs-list__item:hover {
    opacity: 0.7;
    cursor: pointer;
}

.tabs-list__item--active:hover {
    opacity: 1;
    cursor: default;
}

.tabs-list__item--active::before {
    content: "";

    position: absolute;
    top: -16px;
    left: 0;

    width: 150px;
    height: 135px;

    background-image: url('../../image/health/background-active-specialist.png');
    background-repeat: no-repeat;

    z-index: -1;
}

.tabs-list__item--active::after {
    content: "";

    position: absolute;
    bottom: -21px;
    left: 50%;

    width: 23px;
    height: 10px;

    background-image: url("../../image/health/active.png");

    transform: translateX(-50%);
}

.tabs-list__button {
    padding: 0;

    background: none;
    border: none;
}

.tabs-list__wrapper {
    position: relative;

    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.tabs-list__wrapper picture {
    width: 113px;
    height: 106px;
    margin-left: 15px;

    background-color: #dae8f3;
    border-radius: 100px;
}

.tabs-list__item--active picture {
    background-color: rgba(52, 153, 234, 0.55);
    box-shadow: 0px 7px 50px 0 rgba(52, 153, 234, 0.55);
}

.tabs-list__button {
    width: 100%;
}

.tabs-list__name {
    margin-top: 22px;

    font-family: "PlayfairDisplay", "Arial", sans-serif;
    font-size: 17px;
    font-weight: 400;
    color: #b7d1e6;
}

.tabs-list__item--active .tabs-list__name {
    color: #2e2e2e;
}

.tabs-list__logo-image {
    max-width: 97px;
    max-height: 106px;
}

.list-information-about .slick-list {
    overflow: hidden;
}

.list-information-about .slick-track{
    display: flex;
    justify-content: space-between;
}

.list-information-about__item {
    box-sizing: border-box;

    width: 100%;
    min-height: 1165px;
    padding: 20px 26px;

    border-top: 2px solid #3499ea;
    background-color: #ffffff;
}

.list-information-about__item-wrapper {
    display: flex;
    flex-direction: row;
}

.list-information-about__left-block {
    display: flex;
    flex-direction: column;
    max-width: 740.5px;
}

.list-information-about__experience-consulting {
    margin-top: 18px;

    font-family: "PlayfairDisplay", "Arial", sans-serif;
    font-size: 18.6px;
    font-weight: 400;
    color: #2e2e2e;
}

.list-information-about__experience-consulting-number {
    color: #3498e8;

    border-bottom: 1px solid #3498e8;
}

.list-information-about__scope {
    box-sizing: border-box;

    display: flex;
    flex-direction: row;
    margin-top: 17px;

    font-family: "PlayfairDisplay", "Arial", sans-serif;
    font-size: 18.6px;
    font-weight: 400;
    color: #2e2e2e;
}

.list-scope {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.list-scope__item {
    margin-left: 3px;
    padding: 4px 9px;

    font-family: "ProximaNova", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 600;
    color: #3499ea;
    text-transform: uppercase;

    background-color: #ecf4fd;
    border-top-left-radius: 10px;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 3px;
}

.list-scope__item span {
    border-bottom: 1px solid #3499ea;
}

.list-information-about__description {
    margin-top: 19px;

    font-family: "ProximaNova", "Arial", sans-serif;
    font-size: 14px;
    line-height: 1.5;
    font-weight: 300;
    color: #2e2e2e;
}

.list-information-about__description p {
    margin: 10px 0;
}

.list-skills li {
    position: relative;

    padding: 6.5px 0;
    padding-left: 28px;
}

.list-skills li::before {
    content: "";

    position: absolute;
    top: 5px;
    left: -1px;

    width: 20px;
    height: 15px;

    background-image: url('../../image/health/numbering-of-list.svg');
    background-repeat: no-repeat;
}

.list-skills ul li {
    padding: 7.5px 0;
    padding-left: 26px;
}

.list-skills ul li::before {
    top: 50%;
    left: -2px;

    height: 22px;

    background-image: url('../../image/health/numbering-under-of-list.svg');

    transform: translateY(-50%);
}

.choosing-time {
    margin-top: 19px;
    padding: 16px;
    padding-right: 11px;

    border: 1px solid #3499ea;
    border-radius: 4px;
}

.choosing-time__title {
    margin-top: 7px;
    margin-bottom: 10px;
    
    font-family: "PlayfairDisplay", "Arial", sans-serif;
    font-size: 18px;
    font-weight: 400;
    color: #2e2e2e;
}

.choosing-time__note-to-title {
    margin-top: 7px;

    font-family: "ProximaNova", "Arial", sans-serif;
    font-size: 12.5px;
    font-weight: 300;
    color: #2e2e2e;
}

.list-choosing-time {
    margin-top: 18px;
}

.list-choosing-time__item {
    margin-top: 19.5px;
}

.list-choosing-time__item:nth-child(1) {
    margin-top: 0;
}

.list-choosing-time__item-date {
    font-family: "ProximaNova", "Arial", sans-serif;
    font-size: 13.5px;
    font-weight: 600;
    color: #2e2e2e;
    text-transform: uppercase;
}

.list-choosing-hous {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 9px;
}

.list-choosing-hous__item {
    margin-left: 2px;
}

.list-choosing-hous__item button {
    padding: 5px 10px;

    background-color: #ecf4fd;
    border-top-left-radius: 10px;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 3px;
}

/* .list-choosing-hous__item span {
    padding: 5px 10px;
} */

.list-choosing-hous__item--active button {
    color: #ffffff;

    background-color: #3498e9;
}
/* .list-choosing-hous__item--active span { */
    /* border-bottom: 1px solid #ffffff; */
/* } */

.list-choosing-hous__item:hover button {
    color: #3499ea;
}

.list-choosing-hous__item--active:hover button {
    color: #ffffff;
}

/* .list-choosing-hous__item:hover span { */
    /* border-bottom: 1px solid #3499ea; */
/* } */

.list-choosing-hous__item:nth-child(1) {
    margin-left: 0;
}

.choosing-time__choosed {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.choosing-time__choosed-left {
    padding-top: 8px;

    letter-spacing: 0.8px;
    font-weight: 300;
}

.choosing-time__choosed-date {
    display: inline-block;
    margin-top: 9px;
}

.choosing-time__choosed-date, .choosing-time__choosed-hour {
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
}

.choosing-time__choosed-button {
    width: 176px;
    margin-right: 6px;
    height: 56px;

    font-family: "ProximaNova", "Arial", sans-serif;
    font-size: 14.7px;
    font-weight: 600;
    letter-spacing: 0.12px;
    color: #ffffff;
    text-transform: uppercase;

    background-image: url('../../image/health/btn-sign-up.png');
}

.choosing-time__choosed-button:hover{
    opacity: 0.8;
}

.choosing-time__choosed-button:focus{
    opacity: 0.8;
}

.list-information-about__articles {
    margin-top: 51px;
}

.list-information-about__articles__title {
    font-family: "PlayfairDisplay", "Arial", sans-serif;
    font-size: 19px;
    font-weight: 400;
    color: #2e2e2e;
}

.list-articles {

}

.list-articles__item {
    position: relative;

    margin-top: 20px;
}

.list-articles__item::before {
    content: "";

    position: absolute;
    top: 1px;
    left: -1.5px;

    width: 13px;
    height: 13px;

    background-image: url('../../image/health/ellipse-536.svg');
    background-repeat: no-repeat;
}

.list-articles__item-link {
    margin-left: 19px;

    font-size: 14px;
    font-family: "ProximaNova", "Arial", sans-serif;
    font-weight: 300;
    color: #2e2e2e;
}

.list-articles__item-link:hover {
    color: #3498e9;

    border-bottom: 1px solid #3498e9;

    cursor: pointer;
}

.list-information-about__video {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 35px;
}

.list-information-about__video video {
    margin-top: 21px;
}

.list-information-about__video-title {
    width: 100%;

    font-family: "PlayfairDisplay", "Arial", sans-serif;
    font-size: 17px;
    font-weight: 400;
    color: #2e2e2e;
}

@media (max-width: 1296px) {
    .choosing-specialist__list-tabs {
        max-width: 70%;
        margin: 0 auto;
    }

    .list-information-about__item-wrapper {
        flex-direction: column;
    }

    .list-information-about__left-block {
        max-width: 100%;
    }

    .choosing-time {
        display: flex;
        flex-direction: column;
    }

    /* .choosing-time__title {
        margin: 0 auto;

        font-size: 24px;
    }

    .choosing-time__note-to-title {
        margin: 0 auto;
        margin-top: 5px;

        font-size: 22.5px;
    } */

    .choosing-time__choosed {
        flex-direction: column;
    }

    .choosing-time__choosed-button {
        margin-top: 10px;
    }

    .list-information-about__video {
        max-width: 40%;
    }
}

@media (max-width: 849px) {
    .choosing-specialist {
        margin-top: 0;
    }

    .choosing-specialist__list-tabs {
        max-width: 200px;
    }

    .choosing-specialist__list-tabs .slick-slider .slick-prev {
        left: -40px;
    }

    .choosing-specialist__list-tabs .slick-slider .slick-next {
        right: -40px;
    }
}

