.specialistsWorking .slick-slider {
    /* position: relative; */

    min-width: 0;
}

.specialistsWorking .slick-list {
    overflow: hidden;
}
.specialistsWorking .slick-track{
    display: flex;
    flex-direction: row;
}

.slider-counter {
    color: #3499ea;
    font-size: 74px;
}

.slick-prev {
    position: absolute;
    left: 649px;
    bottom: -93px;

    width: 15px;
    height: 15px;

    font-size: 0;

    background-color: transparent;
    background-image: url('../../image/sliderArrowLeft.svg');
    background-repeat: no-repeat;

    z-index: 100;
}

.slick-next {
    position: absolute;
    right: 689px;
    bottom: -93px;

    width: 15px;
    height: 15px;

    font-size: 0;

    background-color: transparent;
    background-image: url('../../image/sliderArrowRight.svg');
    background-repeat: no-repeat;

    z-index: 100;
}

.specialistsWorking .slick-next:hover{
    background-image: url('../../image/sliderArrowRightHover.svg');
}

.specialistsWorking .slick-prev:hover{
    background-image: url('../../image/sliderArrowLeftHover.svg');
}

.specialistsWorking div .slider-work__item--active {
    color: #3499ea;
}

.slider-work__item--active .slider-work__item-svg path {
    fill: #3499ea;
}

.slider-work__item-svg {
    margin-right: 29px;
    cursor: pointer;
}

@media(max-width: 977px){
    .specialistsWorking .slider-work__item-svg {
        width: 40px;
        height: 40px;
    }
}

@media (max-width: 768px) {
    .slick-next {
        right: 20%;
    }

    .slick-prev {
        left: 20%;
    }
}

