.mission {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 951px;
    /* margin-top: -150px; */
}

.mission__item {
    width: 468px;
    height: 467px;

    font-family: 'Playfair Display';
    font-size: 32px;
    line-height: 1.25;
    text-align: center;
    color: #fff;

    background-repeat: no-repeat;
}

.mission__item--self-knowledge-systems {
    background-image: url('../../image/OurMission/space.png');
    background-position: 0px 15px;
}

.mission__item--self-knowledge-systems .link-to-page {
    margin-top: 23%;
}

.mission__item--self-knowledge-systems .link-to-page__text {
    margin-top: auto;
    margin-left: 46%;
    margin-bottom: 19%;
}

.mission__item--fitnes {
    background-image: url('../../image/OurMission/fitnes.png');
}

.mission__item--fitnes .link-to-page__text {
    margin-top: auto;
    margin-right: 57%;
    margin-bottom: 24%;
}

.mission__item--fitnes .link-to-page {
    margin-top: 23%;
}

.mission__item--nutrition {
    background-image: url('../../image/OurMission/avocado.png');
}

.mission__item--nutrition .link-to-page__text {
    margin-bottom: auto;
    margin-left: 55%;
    margin-top: 22%;
}

.mission__item--psychology {
    background-image: url('../../image/OurMission/family.png');
}

.mission__item--psychology .link-to-page__text {
    margin-bottom: auto;
    margin-right: 51%;
    margin-top: 22%;
}

.link-to-page {
    display: flex;
    width: 100%;
    min-height: 77%;

    color: #fff;
}

.link-to-page__text {
    margin: 0 auto;
}

@media (max-width: 951px) {
    .mission__item {
        width: 380px;
    }

    .mission__item--self-knowledge-systems .link-to-page__text {
        margin-left: 37%;
    }
}

@media (max-width: 768px) {
    .mission {
        max-width: 100%;
    }

    .mission__item {
        width: 160px;
        height: 119px;

        background-size: cover;
    }

    .mission__item  .link-to-page {
        margin: 0;
        min-height: 100%;
    }

    .mission__item .link-to-page__text {
        margin: 0 auto;
        margin-top: 45px;

        font-size: 12px;
    }

    .mission__item--nutrition .link-to-page__text {
        margin-left: 45%;
    }

    .mission__item--self-knowledge-systems .link-to-page__text {
        margin-left: 45%;
        margin-top: 45%;
    }

    .mission__item--fitnes .link-to-page__text {
        margin-right: 62%;
        margin-top: 50%;
    }

    .mission__item--psychology .link-to-page__text {
        margin-right: 45%;
    }

