
.slick-slider {
    position: relative;

    min-width: 0;
}

.slick-list {
    overflow: hidden;
}
.slick-track{
    display: flex;
    flex-direction: row;
}

.slider-counter {
    color: #3499ea;
    font-size: 74px;
}

.slick-prev {
    position: absolute;
    left: 669px;
    bottom: -95px;

    width: 15px;
    height: 15px;

    font-size: 0;

    background-color: transparent;
    background-image: url('../../image/sliderArrowLeft.svg');
    background-repeat: no-repeat;

    z-index: 100;
}

.slick-next {
    position: absolute;
    right: 678px;
    bottom: -95px;

    width: 15px;
    height: 15px;

    font-size: 0;

    background-color: transparent;
    background-image: url('../../image/sliderArrowRight.svg');
    background-repeat: no-repeat;

    z-index: 100;
}

.specialistsWorking .slick-next:hover{
    background-image: url('../../image/sliderArrowRightHover.svg');
}

.specialistsWorking .slick-prev:hover{
    background-image: url('../../image/sliderArrowLeftHover.svg');
}
